/* topbar start here */

#topbar {
    z-index: 1000;
    width: 100%;
    padding: 0 30px;
    overflow: hidden;
    top: 0;
}

#topbar.topbar-dark {
    background: #202020;
}

#topbar a {
    text-decoration: none;
    color: #606060;
}

#topbar.text-light a {
    color: #ffffff;
}

.topbar-left,
.topbar-right {
