.arrow-up,
.arrow-down {
    width: 0;
    height: 0;
    border-left: 40px solid transparent;
    border-right: 40px solid transparent;
    border-bottom: 40px solid #fff;
    position: fixed;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    bottom: 0;
    cursor: poInter;
    z-index: 101;
}

.arrow-down {
    border-top: 40px solid #fff;
    border-bottom: none;
    position: fixed;
    z-index: 102;
    top: -40px;
}

.arrow-up:before {
    font-family: "FontAwesome";
    font-size: 20px;
    content: "\f067";
    color: #222;
    position: relative;
    top: 12px;
    margin: 0 0 0 -8px;
}

.arrow-down:before {
    font-family: "FontAwesome";
    font-size: 20px;
    content: "\f068";
    color: #222;
    position: relative;
    top: -35px;
    margin: 0 0 0 -8px;
}

