/* dropdown */
/* ------------------------------ */
.dropdown {
    position: relative;
    z-index: 110;
    font-weight: 600;
    display: inline-block;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
}

.dropdown a,
.dropdown a:hover {
    text-decoration: none;
    outline: none;
    display: inline-block;
}

.dropdown a,
.header-light .dropdown a:hover {
    color: #333;
}

.dark-scheme .dropdown a,
.dark-scheme .header-light .dropdown a:hover {
    color: #ffffff;
}

.dropdown>a {
    display: inline-block;
    padding: 7px 12px 7px 12px;
    min-width: 140px;
    border: solid 1px rgba(0, 0, 0, .2);
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
    font-weight: bold;
}

.dark-scheme .dropdown>a{
    border: solid 1px rgba(255, 255, 255, .2);
}

.dropdown>a:after {
    font-family: "FontAwesome";
    font-size: 16px;
    content: "\f107";
    position: relative;
    float: right;
    margin-left: 10px;
}

.dropdown ul,
.dropdown li {
    list-style: none;
    display: block;
    padding: 0;
    margin: 0;
}

.dropdown ul {
    position: absolute;
    min-width: 140px;
    display: none;
    height: 0;
    cursor: pointer;
}

.dropdown li span {
    background: #ffffff;
    display: block;
    padding: 5px 10px 5px 10px;
    border: solid 1px rgba(0, 0, 0, .2);
    border-top: none;
    width: 100%;
    font-weight: 400;
}

.dark-scheme .dropdown li span{
    background:#21273e;
    border: solid 1px rgba(255, 255, 255, .1);
}

.dark-scheme .dropdown li span:hover{
    background: #161D30;
    color: #fff;
}

.dropdown li span:hover {
    background: #eeeeee;
}

.dropdown li.active {
    display: none;
}

.text-light .dropdown {
    background: none;
    border: solid 1px rgba(255, 255, 255, .1);
}

.text-light .dropdown a {
    color: #ffffff;
}

.items_filter {
    font-size: 14px;
    margin-bottom: 30px;
}

.items_filter .dropdown {
